@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap);
.container{
    position: absolute;
    z-index: 1002;
    top: 20%;
    left: 20%;
    width: 25.2vw;
    /* box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5); */
    /* background-color: rgba(255, 255, 255, 0.1); */
    overflow: hidden;
    /* border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5); */
    padding: 1.5vw;
}

.name{
    color:rgb(255, 255, 255);
    font-size: 8vw;
    margin: 0;
    line-height: 9.5vw;
    font-family: 'Oswald', sans-serif;
    text-shadow: 3px 4px 7px rgba(0, 0, 0, 0.73);
    z-index: 1000;
    

}

.dev{
    font-size:2vw;
    margin: 0;
    position: relative;
    letter-spacing: .15vw;
    font-family: 'Oswald', sans-serif;
    width: 13.7vw;
    left: 8.3vw;
    padding-left: 0.4vw;
    
    bottom: .6vw;
    background-color:black;
    color: white;
    z-index: -1;
}

.Tilt{
    
    position: absolute;
    z-index: 1001;

}

@media(max-width:474px){
    .container{
        top: 64%;
        left: 50%;
        transform: translate(-50%);
        z-index: 1001;
    }

    .dev{
        background-color:white;
        font-size: 3vw;
        text-align:center;

    }
}



.blackNav{
    color: white;
    background-color: black;
    position: fixed;
    height: 100%;
    width: 30%;
}

.socialMediaContainer{
    color: rgb(0, 0, 0);
    background-color:white;
    width: 2.5vw;
    top: 50%;
    position: absolute;
    transform: translateY(-50%);
    padding-top:.5vw;
    padding-bottom: .5vw;
    
}

span{
    color: rgb(0, 0, 0);
    padding:1vw;
    font-family: 'Share Tech', sans-serif;;
}

.socialMedia{
    background-color: white;
    font-size:1.7vw;
    margin-top: 1vw;
    margin-bottom:1vw;
    text-align: left;
    text-align: initial;
    margin-left: 15%;
    width: 0px;
    display: block;
    
}



@media(max-width:474px){
    .blackNav{
        position: fixed;
        width: 100%;
        height: 30%;
        bottom: 0%;
    }
}
.navigationPanel{
    position: absolute;
    left: 30%;
    width: 70%;
    height: 94.1%;
    
}

nav{
    display:flex;
    justify-content: space-around;
    align-items: center;
    min-width: 10vh;
}

.nav-links{
    width: 35%;
    display: flex;
    justify-content:space-around;
    list-style: none;
    font-size: 1.5vw;
    font-family: 'Oswald', sans-serif;
    position: relative;
    left: 18vw;
    margin: 10px;
}

a{
    color: rgb(0, 0, 0);
    text-decoration:none;
    text-align: center;
    
    margin-left: 1vw;
}

/* a:hover{
    background-color: rgb(0, 0, 0);
    color:white;
} */

.active{
    background-color: rgb(0, 0, 0);
    color: white;
    
}

a li{
    
    width: 6vw;
    display: flex;
    position: relative;
}



.homeImage{
    position: fixed;
}
/* DONT TOUCH */
body{
    margin: 0;
    padding: 0;
}
